$color-emphasis: if(
    lightness(deep-get($theme, 'colors.negative')) < lightness(deep-get($theme, 'colors.positive')),
    lighten(deep-get($theme, 'colors.positive'), 40%),
    darken(deep-get($theme, 'colors.positive'), 20%)
);
