@import "../../config/config";

$theme: (
  name: 'dark',
  version: '0.0.0',
  description: 'Default dark theme.',
  author: 'TheoryOfNekomata <allan.crisostomo@outlook.com',
  colors: (
    positive: #999,
    //positive: #666,
    //negative: #fff,
    negative: #333,
    primary: #996699,
    accents: (
      #ff0066,
      #ffcc00,
      #33cc33,
      #3399ff
    ),
    shades: .15 .25 .35 .5 .75
  ),
  type: (
    fonts: (
      body: (
        font-family: ('Open Sans', 'M+ 2p', sans-serif),
        line-height: 1.618033989,
        font-weight: 300
      ),
      headings: (
        font-family: ('Open Sans Condensed Light', 'Open Sans Condensed', 'Open Sans', 'M+ 2p', sans-serif),
        //font-family: ('Yanone Kaffeesatz', 'Open Sans Condensed', 'Open Sans', 'M+ 2p', sans-serif),
        line-height: 1.1,
        font-weight: 200,
        text-transform: lowercase
      ),
      code: (
        font-family: (Mononoki, Consolas, monospace),
        line-height: 1.1
      )
    ),
    font-size: 12px
  ),
  lengths: (
    grid: 12px
  ),
  animations: (
    transition: 500ms ease
  ),
  components: (
    breadcrumbs: (
      separator: '/'
    )
  )
);

